const isEmpty = (value) =>
  value.trim().length > 0 ? undefined : '  ';

const checkValidation = (word) => {
  const newErrors = Object.keys(word).reduce((errors, item) => {
    let result = { ...errors };

    switch (item) {
      case 'english':
        result = {
          ...errors,

          [item]: /^[a-z]*$/i.test(word[item])
            ? isEmpty(word[item])
            : '   ',
        };
        break;
      case 'transcription':
        result = {
          ...errors,
          [item]: isEmpty(word[item]),
        };
        break;
      case 'russian':
        result = {
          ...errors,
          [item]: /^[-]*$/i.test(word[item])
            ? isEmpty(word[item])
            : '   ',
        };
        break;
      case 'tags':
        break;
      default:
    }

    return result;
  }, {});

  return newErrors;
};
export default checkValidation;
